section {
  padding: 60px 0;
}
section h2 {
  font-family: 'Sanchez';
  color: #fff;
  font-style: italic;
  text-transform: uppercase;
  font-size: 24px;
}
section#news {
  padding: 0;
  background: url(http://placehold.it/1920x600) no-repeat center center;
  width: 100%;
  height: 600px;
  padding: 80px 0;
  background-size: cover;
}
.navbar {
  background: rgba(255, 255, 255, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  border: 0;
  border-radius: 0;
  font-family: 'Sanchez';
}
.navbar .container {
  width: 80%;
}
.navbar h1.logo {
  text-indent: -9999px;
  background: url(../img/logo-stillpoint.png) no-repeat center center;
  width: 262px;
  height: 150px;
  margin: 20px 0;
}
.navbar .navbar-right {
  margin-top: 68px;
}
.navbar .navbar-right li a {
  text-transform: uppercase;
  font-size: 18px;
  padding: 10px 25px;
  margin-right: 40px;
  color: #439e7c;
  position: relative;
}
.navbar .navbar-right li a:hover {
  color: #439e7c;
}
.navbar .navbar-right li a:hover:after {
  position: absolute;
  content: '';
  bottom: 0px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 8px;
  height: 8px;
  background: #439e7c;
  border-radius: 50%;
}
.main-carousel .slide {
  background: url(http://placehold.it/1920x500) no-repeat center center;
  width: 100%;
  height: 800px;
  background-size: cover;
}
section#about .about-box {
  padding: 0 80px;
  margin: auto;
}
section#about .about-box .about-img {
  position: relative;
}
section#about .about-box .about-img h2.title {
  position: absolute;
  bottom: 0;
  width: 100%;
  left: 0;
  background: #439e7c;
  padding: 10px 25px;
  margin: 0;
  text-transform: none;
  font-size: 20px;
}
section#about .about-box img {
  width: 100%;
}
section#about .about-box p {
  text-align: justify;
  padding: 30px 0;
}
section#attivita .attivita-box {
  padding: 60px;
}
section#attivita p {
  color: #fff;
  font-family: 'Open Sans';
}
section#attivita h2 {
  position: relative;
}
section#attivita h2:after {
  position: absolute;
  content: '';
  bottom: -20px;
  left: 0;
  width: 100%;
}
section#attivita .container-fluid .col-md-6 {
  padding: 0;
}
