section#gallery .col-md-3 img {
  border-radius: 50%;
  margin: 25px auto;
}
section#news .news {
  padding: 20px;
  margin: 0 40px;
  background: #fff;
  border-radius: 7px;
  height: 90%;
}
section#quotes .slick-prev {
